import { Component, OnInit } from '@angular/core';
import { ComputerService } from 'src/app/services/computer.service';
import { Computer } from 'src/app/model/computer.model';

@Component({
  selector: 'app-archivio',
  templateUrl: './archivio.component.html',
  styleUrls: ['./archivio.component.css'],
})
export class ArchivioComponent implements OnInit {

  constructor(private computerService: ComputerService) {

    computerService.loadComputers();

   }

  get computer(): Computer[] {
    return this.computerService
      .getComputer(this.marcaSelezionata);
  }

  get marche(): string[] {
    return this.computerService.getMarche();
  }

  marcaSelezionata: string;

  selezionaMarca(marca: string) {
    this.marcaSelezionata = marca;
  }

  ngOnInit() {
  }

}
