import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ComputerService } from 'src/app/services/computer.service';

@Component({
  selector: 'app-gestione',
  templateUrl: './gestione.component.html',
  styleUrls: ['./gestione.component.css']
})
export class GestioneComponent implements OnInit {

  formComputers: FormGroup;

  constructor(private computerService: ComputerService) { }

  ngOnInit() {
    this.computerService.loadComputers();

    this.formComputers = new FormGroup({
      marca: new FormControl('', [ Validators.required ] ),
      nome: new FormControl('', [ Validators.required ] ),
      os: new FormControl('', [ Validators.required ] ),
      hd: new FormControl('', [ Validators.required ] ),
      ram: new FormControl('', [ Validators.required ] ),
      cpu: new FormControl('', [ Validators.required ] ),
      seriale: new FormControl('', [ Validators.required ] ),
      attivo: new FormControl(''),
      note: new FormControl(''),
      dipendente: new FormControl(''),
    });
  }

  salvaComputers() {
    this.computerService.createComputers(this.formComputers.value)
      .subscribe((res) => {
        this.computerService.loadComputers();
        this.formComputers.reset();
      });
  }

}
