import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { ArchivioComponent } from '../magazzino/archivio/archivio.component';
import { GestioneComponent } from '../magazzino/gestione/gestione.component';
import { HomeComponent } from '../home/home.component';
import { AggiornamentoArchivioComponent } from '../magazzino/aggiornamento-archivio/aggiornamento-archivio.component';




@NgModule({
  declarations: [],
  imports: [
    CommonModule,
    RouterModule.forRoot([
      { path: '', component: HomeComponent },
      { path: 'archivio', component: ArchivioComponent },
      { path: 'aggiornamento/:id', component: AggiornamentoArchivioComponent },
      { path: 'gestione', component: GestioneComponent },
    ])
  ]
})
export class RoutingModule { }
