import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Computer } from '../model/computer.model';

@Injectable({
  providedIn: 'root'
})
export class ComputerService {

  constructor(private httpClient: HttpClient) { }

    computer_id: number;

    private _COMPUTERS: Computer[] = [
		new Computer(1, 'Acer', '5552 series-pew76', 'Win 7 Ultimate', '447 GB SSD', '8 GB DDR3-1600 max 8', 'AMD Athlon II P320 2.10 GHz',   
		'YTO2203050036152F12701','si','','Bianchi Mario'),
		new Computer(2, 'Lenovo', 'B50-50', 'Win 10 Home', '450 GB no SSD', '4 GB DDR3L-1600 max 8', 'i3 5005U 2 GHz',   
		'WD02ER01','no','In attesa di sostituzione HD'),
		new Computer(3, 'Toshiba', 'SATELLITE C50D-A-12R', 'Win 8.1', '467 GB SATA', '12 GB DDR3L-1600 max 16', 'AMD A-500 1.5 GHz',   
		'YD0788665','no','Disco SSD non installabile nonostante la clonazione.'),
		new Computer(4, 'HP', '15-da0197nl', 'Win 10 Home', '447 GB SSD', '8 GB DDR4-2133 max 16', 'i3 M380 2.53 GHz',   
		'CPN45224Z5','si','','Verdi Giuseppe')
	];

    loadComputers() {

        const observable: Observable<Computer[]> =
        this.httpClient.get<Computer[]>('computer'); // ('http://localhost:3000/computers')

        observable.subscribe(
            (response) => {
                this._COMPUTERS = response;
				// this.computer_id = params ['id'];
            },
            (error) => {console.log(error); }
        );

    }

    getComputer(marca?: string): Computer[] {

        if (marca) {

            return this._COMPUTERS.filter(
                com => com.marca === marca
            );

        } else {

        return this._COMPUTERS;

        }

    }

    getMarche(): string[] {

        return this._COMPUTERS

        .map( (com) => com.marca )

        .filter( (marca, index, array) => array.indexOf(marca) === index )

        .sort();

    }

    createComputers(computers: Computer): Observable<Computer> {
        return this.httpClient.post('http://localhost:3000/computers', computers);
    }

    updateComputers(computers: Computer): Observable<Computer> {
        return this.httpClient.put('http://localhost:3000/computers', computers);
    }

}
